              v-for = "item in filterData" :key = "item.StopID"><span class="me-2">
                <!-- 3 -->
                {{item?.EstimateTime === undefined ? getNextBusTime(item.NextBusTime): showArriveBusTime(item.EstimateTime )}}</span> {{item.StopName.Zh_tw}}
